import React, { Component, Fragment } from 'react';

import { Card, Row, Table, message, Popconfirm, Col } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { getListStudyField, deleteStudyField } from 'services/StudyField';
// import { specialized } from 'dump/data';

class Specialized extends Component {
  constructor(props) {
    super(props);
    this.state = {
      specialized: [],
      loading: false,
      pagination: {},
    };
  }
  componentDidMount() {
    this.getListData();
  }

  getListData = page => {
    this.setState({
      loading: true,
    });
    getListStudyField(page, true)
      .then(res => {
        const pagination = { ...this.state.pagination };
        pagination.total = res.data.totalResults;
        this.setState({
          specialized: res.data.items,
          loading: false,
          pagination,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = (pagination, filters, sorter) => {
    const pager = { ...this.state.pagination };
    pager.current = pagination.current;
    this.setState({
      pagination: pager,
    });
    this.getListData(pagination.current);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deleteStudyField(e)
      .then(() => {
        message.success('Xóa ngành đào tạo thành công');
        this.getListData();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        this.setState({
          loading: false,
        });
        console.log('res', error);
        message.error(error.response.data.message);
      });
  };

  render() {
    const { pagination, specialized, loading } = this.state;
    this.columns = [
      {
        title: 'Tên ngành',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Mã ngành',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên tiếng anh',
        dataIndex: 'labelInEnglish',
        key: 'labelInEnglish',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          specialized.length >= 1 ? (
            <Fragment>
              <BtnEdit href={`#/specaialize/form/edit/${record.id}`} />
              <Popconfirm title="Bạn có muốn xóa?" onConfirm={() => this.confirmDelete(record.id)}>
                <BtnDelete />
              </Popconfirm>
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Card title="Danh sách ngành đào tạo">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/specaialize/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table
              bordered
              dataSource={specialized}
              columns={this.columns}
              pagination={pagination}
              onChange={this.handleTableChange}
              loading={loading}
            />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <Specialized />
  </Page>
);



